.cube {
  margin: 0 auto 3rem;
  top: 50%;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transform: rotateX(-1deg) rotateY(-1deg) rotateZ(-1deg);
          transform: rotateX(-1deg) rotateY(-1deg) rotateZ(-1deg);
  -webkit-animation: rotate 15s linear infinite;
          animation: rotate 15s linear infinite;
  height: 6.5rem;
  width: 6.5rem;
}

.cube div {
  height: 6.5rem;
  width: 6.5rem;
  background: transparent;
  position: absolute;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.inner {
  background: none;
  border: 1px dashed var(--color-gray);
  border-radius: 50%;
}

.top {
  -webkit-transform: rotateX(90deg) translateZ(calc(6.5rem * 2));
          transform: rotateX(90deg) translateZ(calc(6.5rem * 2));
}

.bottom {
  -webkit-transform: rotateX(90deg) translateZ(-calc(6.5rem * 1.2));
          transform: rotateX(90deg) translateZ(-calc(6.5rem * 1.2));
}

.sphere-one {
  -webkit-transform: rotateX(0) translateZ(0);
          transform: rotateX(0) translateZ(0);
}

.sphere-two {
  -webkit-transform: rotateY(-90deg) translateZ(0);
          transform: rotateY(-90deg) translateZ(0);
}

.sphere-three {
  -webkit-transform: rotateX(90deg) translateY(0);
          transform: rotateX(90deg) translateY(0);
}

.sphere-three {
  -webkit-transform: rotateY(45deg) rotateX(45deg) translateY(0);
          transform: rotateY(45deg) rotateX(45deg) translateY(0);
}

.sphere-five {
  -webkit-transform: rotateY(-45deg) rotateX(-45deg) translateY(0);
          transform: rotateY(-45deg) rotateX(-45deg) translateY(0);
}

.sphere-six {
  -webkit-transform: rotateY(-45deg) rotateX(45deg) translateY(0);
          transform: rotateY(-45deg) rotateX(45deg) translateY(0);
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotateX(-1deg) rotateY(-1deg) rotateZ(-1deg);
            transform: rotateX(-1deg) rotateY(-1deg) rotateZ(-1deg);
  }

  50% {
    -webkit-transform: rotateX(180deg) rotateY(180deg) rotateZ(180deg);
            transform: rotateX(180deg) rotateY(180deg) rotateZ(180deg);
  }

  100% {
    -webkit-transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
            transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotateX(-1deg) rotateY(-1deg) rotateZ(-1deg);
            transform: rotateX(-1deg) rotateY(-1deg) rotateZ(-1deg);
  }

  50% {
    -webkit-transform: rotateX(180deg) rotateY(180deg) rotateZ(180deg);
            transform: rotateX(180deg) rotateY(180deg) rotateZ(180deg);
  }

  100% {
    -webkit-transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
            transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
  }
}

