  position: absolute;
  content: "";
  top: -0.375rem;
  left: -0.375rem;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 1px solid var(--color-gray);
}

.footer-content-about {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 2rem;
  gap: 2rem;
  position: relative;
}

.footer-content-about-txt {
  left: 0;
  top: 0;
  width: 100%;
  text-align: center;
  position: absolute;
}

.footer-links {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.footer-link {
  border-radius: 50%;
  padding: 0.25rem 0.5rem;
  background: var(--color-black);
}

.footer-link .icon {
  width: 1rem;
  height: 1rem;
}

.cursor-wrapper {
  position: fixed;
  pointer-events: none;
  mix-blend-mode: difference;
  z-index: 10000;
}

.cursor {
  width: 0.65rem;
  height: 0.65rem;
  background-color: white;
  border-radius: 50%;
  position: absolute;
  margin-top: -4rem;
  mix-blend-mode: difference;
}

.cursor-outline {
  mix-blend-mode: difference;
  margin-top: -4rem;
  --size: 2.5rem;
  transition: all 0.15s;
  position: absolute;
  top: calc(var(--size) / -2);
  left: calc(var(--size) / -2);
  border-radius: 50%;
  width: var(--size);
  height: var(--size);
  border: 2px solid white;
}

.App {
  height: 100vh;
  width: 100vw;
}

.main {
  position: relative;
  margin: auto;
  max-width: var(--width-desktop);
}

.message-box {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: var(--color-black);
}

@media screen and (max-width: 1000px) {
  :root {
    --width-desktop: 40rem;
  }

  .home-scroll {
    display: none;
  }

  .footer-go-to-top {
    display: none !important;
  }

  .home-settings {
    margin-top: 1rem;
    position: static;
    position: initial;
  }

  .settings {
    flex-direction: row;
  }

  .footer-content-about {
    flex-direction: row !important;
  }

  .footer-content-about-txt {
    top: 2rem !important;
  }

  .pinpoint {
    margin: 1rem 0;
    right: unset !important;
    position: relative !important;
  }
}

